$font: "Helvetica Neue", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
  Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;

//Colors
$white: #fff;
$grey: #444444;
$black: #000;
$yellow: #F8EE1F;
$maroon: #83333e;
$off-white: #f2f4f5;

html,
body {
  font-family: $font;
  font-size: 16px;
  padding: 0;
  margin: 0;
  background-color: $black;
}

.carousel {
  width:900px;
  height:500px;
  margin: auto;

  @media (max-width: 900px) {
    .carousel {
      width:auto;
      height:auto;
    }
}
}

